@import url(https://fonts.googleapis.com/css?family=Julius+Sans+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300&display=swap);
@import url(https://fonts.googleapis.com/css?family=IM+Fell+DW+Pica&display=swap);
body {
	background-color: #e9e5e3;
}
.font-title {
	font-family: 'IM Fell DW Pica', serif;
}

.font-subtitle {
	font-family: 'Julius Sans One', sans-serif;
}

.font-main {
	font-family: 'Open Sans Condensed', sans-serif;
}

.bg-offwhite {
	background-color: #e9e5e3;
}

.bg-light-bluegray {
	background-color: #aab1b3;
}

.bg-lighttan {
	background-color: #c09e72;
}

.bg-darktan {
	background-color: #9e6228;
}

.fc-offwhite {
	color: #e9e5e3;
}

.fc-light-bluegray {
	color: #aab1b3;
}

.fc-dark-bluegray {
	color: #394545;
}

.fc-lighttan {
	color: #c09e72;
}

.fc-darktan {
	color: #9e6228;
}

a {
	color: #394545;
}

a:visited {
	color: #394545;
}
.headerButton {
  background: transparent;
  border: none;
  outline: none;
}

.headerButton:hover {
	color: #e9e5e3;
	-webkit-transition: .5s;
	transition: .5s;
}

.header-background {
	background-image: url(/static/media/ep_naturalwhite.110a9bd2.png);
}
.card-height {
	height: 300px;
}

.card-grid {
	display: grid;
	grid-template-rows: 1fr 4fr 1fr 1fr;
}

.card-background {
	background-image: url(/static/media/dust_scratches.608ab5fe.png);
	background-size: cover;
}

@media only screen and (max-width: 48em) {
	.card-height {
		height: 400px;
	}
}
